import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'sort'
})
export class SortPipe implements PipeTransform {

  transform(a,col,toggle): any {
    let b=a

    if(col=="id")
    {
    	b=b.sort((x,y)=>{
    		return toggle?(x.id-y.id):(y.id-x.id)
    	})
    }
    else
    {
    	b=b.sort((x,y)=>{
    		if(toggle)
    		{
    			if(x[col]<y[col])
    			{

    				return 1
    			}
    			else
    			{
    				return -1
    			}
    			return 0
    		}else{
	    		if(x[col]>y[col])
	    		{

	    			return 1
	    		}
	    		else
	    		{
	    			return -1
	    		}
	    		return 0
    		}
    	})
    }
    return b;
  }

}
