import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  template:`
	<h1> total records {{a.length}}</h1>
	<input (keyup.enter)="s=$event.target.value">
	<!-- {{s}} -->
	<table>
		<thead>
			<tr>
				<th (click)="mySort('id')">id</th>
				<th (click)="mySort('name')">name</th>
				<th (click)="mySort('email')">email</th>
			</tr>
		</thead>
		<tbody>
			<tr *ngFor="let x of a|sort:col:toggle|search:s">
				<td>{{x.id}}</td>
				<td>{{x.name}}</td>
				<td>{{x.email}}</td>
			</tr>
		</tbody>
	</table>




  `
})
export class AppComponent {
  a=[
		{id:1,name:"demo1",email:"a@gmail.com"},
		{id:2,name:"demo2",email:"b@gmail.com"},
		{id:3,name:"demo3",email:"x@gmail.com"},
		{id:6,name:"demo5",email:"z@gmail.com"},
		{id:4,name:"demo3",email:"g@gmail.com"},
		{id:5,name:"demo2",email:"h@gmail.com"},
		{id:9,name:"demo9",email:"l@gmail.com"},
		{id:8,name:"demo7",email:"a@gmail.com"},
  ]
  col="id"
  toggle=false
  s=""

	mySort(column)
	{
		this.toggle=!this.toggle
		this.col=column
	}



}
